let ScripeBase = require('ScriptBase')
cc.Class({
    extends: ScripeBase,

    properties: {
        accel: 0
    },

    onInit(){
        this.speed = 0;
    },

    onUpdate(dt) {
        this.speed += this.accel * dt;
        this.node.y += this.speed * dt;
        this.node.angle -= 100 * dt
        if (this.node.angle <= -90) {
            this.node.angle = -90;
        }
    },
    jump() {
        this.speed = 400
        this.node.angle = 60
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    // start () {},

    // update (dt) {},
});
